import axios from 'axios'
// axios.defaults   
// 
import JSONbigin from 'json-bigint' //  
import store from '@/store'
import router from '@/router'
// axios.create() //  new  axios 
const instance = axios.create({ //  new axios ! ! ! !

  //  &
  baseURL: 'http://ttapi.research.itcast.cn/app/v1_0', //     ! ! ! !
  //    axios       

  transformResponse: [function (data) { // data   
    //   data   
    return data ? JSONbigin.parse(data) : {} //     JSON.parse
  }]
})

//  token   ! ! ! !
instance.interceptors.request.use(function (config) {
  if (store.state.user.token) {
    config.headers.Authorization = `Bearer ${store.state.user.token}` // token headers
  }
  return config // 
}, function (error) { //  Promise 
  return Promise.reject(error) // 
})

//  
instance.interceptors.response.use(function (response) {
  // axios  data     data datadata  
  return response.data.data ? response.data.data : response.data // 2 data     dataaxiosdata
}, async function (error) { //  Promise 
  /**
     *    401 token  
     *    error 
     *    config     config  25 config
     *    request 
     *    response  status  
     */
  if (error.response && error.response.status === 401) {
    const path = {
      url: '/login', // 
      query: { //     query   query 
        redirectUrl: router.currentRoute.fullpath //   
      }

    }
    //  401  token  token
    // refresh_token  token 
    if (store.state.user.refresh_token) {
      //  refresh_token
      // token   refresh_token token
      try {
        const ser = await axios({
          method: 'put', // 
          url: 'http://ttapi.research.itcast.cn/app/v1_0/authorizations', // url
          headers: { Authorization: `Bearer ${store.state.user.refresh_token}` } //   refresh_token
        })
        //  token
        // ser.data.data.token
        //    token
        store.commit('updataUser', {
          user: {
            token: ser.data.data.token, // token
            refresh_token: store.state.user.refresh_token //  refresh_token 14
          }
        })
        //     instance  
        return instance(error.config) //    401   return    
      } catch (error) {
        //  
        //     token  token refesh_token
        store.commit('deldataUser') //  token  token
        router.push(path)
      }
    } else {
      //   refresh_token  over 
      // router.push('/login') //     uot  
      //      
      // router.currentRoute    
      //     router.currentRoute.fullpath  
      store.commit('deldataUser') //  token  token
      router.push(path)
    }
  }
  return Promise.reject(error) // 
})
//    
export default instance // instance  axios 
